.sidebar {
  width: 350px;
  height: 100%;
  background-color: #333;
  color: white;
  position: fixed;
  top: 0;
  left: -295px;
  transition: left 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding-top: 60px; /* Add padding for title */
}

.sidebar.expanded {
  left: 0;
}

.sidebar-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100px;
  padding: 8px 0;
}

.sidebar-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 300px;
  padding: 10px 0;
}

.sidebar-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 250px;
  padding: 100px 0;
}

.sidebar-header {
  border-bottom: 1px solid #555; /* Optional: Add a border to separate the header */
}

.sidebar-footer {
  border-top: 1px solid #555; /* Optional: Add a border to separate the footer */
  margin-top: auto; /* Push the footer to the bottom */
}

.title-button {
  background-color: transparent;
  border: none;
  color: white;
  font-weight: bold;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  width: 100%;
  cursor: default;
}

.add-button,
.load-button,
.generate-button,
.redo-button {
  background-color: #3498db;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  width: 60%;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
  margin-bottom: 20px;
}

.save-button,
.undo-button,
.reset-button {
  background-color: #3498db;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  width: 60%;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
  margin-bottom: 10px;
}

.add-button:hover,
.save-button:hover,
.load-button:hover,
.generate-button:hover,
.redo-button:hover {
  background-color: #2980b9;
}

.undo-button:hover,
.reset-button:hover {
  background-color: #b92929;
}

/* AddBlockPopup.css */

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: 1px solid #ccc;
  padding: 20px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popup input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.popup button {
  background-color: #3498db;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  width: 100px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
  margin: 0 10px;
}

.popup button:hover {
  background-color: #2980b9;
}

.popup button:last-child {
  background-color: #ccc;
  color: black;
}

.popup button:last-child:hover {
  background-color: #b0b0b0;
}
